import { Component, OnInit } from '@angular/core';
// import { getDiffieHellman } from 'crypto';
import { ActivatedRoute, Router } from '@angular/router';
import { AppService } from 'src/app/app.service';

@Component({
  selector: 'app-user-detail',
  templateUrl: './user-detail.component.html',
  styleUrls: ['./user-detail.component.css']
})
export class UserDetailComponent implements OnInit {
  id: any;
  userDetail: any;
  image="data:image/jpeg;base64,"

  constructor(public router : Router,private activatedRoute: ActivatedRoute,public service :AppService) { }

  ngOnInit() {
    this.getId()
    this.getUserData()
  }

  getId(){
    this.activatedRoute.params.subscribe(paramsId => {
      this.id = paramsId.id;
      console.log(this.id);
    });
  }

  getUserData(){
    let apireq={
      userId : this.id
    }
    this.service.postApi('/api/v1/admin/getUserDetail',apireq,1).subscribe((success)=>{
      // console.log(success)
      if(success.response_code==200){
        this.userDetail = success.Data
        this.image = success.Data.profilePic
        console.log("userDetails",this.userDetail)
        console.log(this.image)
      }
      else{
        console.log(success.response_message)
      }
    },error=>{
      console.log("Something went wrong")
    })
  }
}
