import { Component, OnInit } from '@angular/core';
import { AppService } from 'src/app/app.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-report-detail',
  templateUrl: './report-detail.component.html',
  styleUrls: ['./report-detail.component.css']
})
export class ReportDetailComponent implements OnInit {
  reportID: string;
  temp: any = {};
  item: any = {};

  constructor(
    private serviceCaller : AppService,
    private activatedRoute: ActivatedRoute,

  ) { 
    this.reportID = activatedRoute.snapshot.url[1].path;
  }

  ngOnInit() {
    this.getDetail();
  }


  getDetail() {
    this.temp.userId = localStorage.getItem('_id');
    this.temp.reportId = this.reportID;
    this.serviceCaller.postApi("/api/v1/admin/reportDetail", this.temp, 0).subscribe((data:any) => {
      console.log('$$$$$$$$$$$$$$$', data);
      if( data.response_code == 200 ) {
        this.item = data.result[0];
      }
    });
  }

}
