import { Component, OnInit, ViewChild } from '@angular/core';
import { AppService } from 'src/app/app.service';
import { Router, ActivatedRoute } from '@angular/router';
import { EmbedVideoService } from 'ngx-embed-video';
import { DomSanitizer, SafeResourceUrl, SafeUrl} from '@angular/platform-browser';
@Component({
  selector: 'app-post-detail',
  templateUrl: './post-detail.component.html',
  styleUrls: ['./post-detail.component.css']
})
export class PostDetailComponent implements OnInit {

  temp:any = {};
  postID:any = {};
  item:any = {};
    spl:any
  @ViewChild('videoPlayer') videoplayer: any;
  iframe_html: any;

  constructor(
    private serviceCaller : AppService,
    private activatedRoute: ActivatedRoute,
    private embedService : EmbedVideoService,
    private senetizer: DomSanitizer
  ) {
    this.postID = activatedRoute.snapshot.url[1].path;
   }

  ngOnInit() {
    this.getDetail();
  }

  getDetail() {
    this.temp.userId = localStorage.getItem('_id');
    this.temp.postId = this.postID;
    this.serviceCaller.postApi("/api/v1/admin/getPostDetail", this.temp, 0).subscribe((data:any) => {
      // console.log('$$$$$$$$$$$$$$$', data);
      if( data.response_code == 200 ) {
        this.item = data.result;
        let URL=data.result.video
        this.spl=URL.split(".")
        console.log(`this.split`,this.spl)
        this.spl[this.spl.length-1]='mp4'

        console.log(`this.split join=====>>`,this.spl)
        this.spl=this.spl.join(".")
           console.log(`this.split afterjoin=====>>`,this.spl)
        // // var endType=spl[spl.length-1]
        // console.log('sssssssssssssssssssssssssss1----->',URL)
        // this.spl.pop();
        // this.spl.push('mp4')
        // console.log('sssssssssssssssssssssssssss2----->',this.sql)
        // this.sql.join(".")
        // console.log('sssssssssssssssssssssssssss3----->',this.sql)

        // this.iframe_html = this.embedService.embed(data.result.video);

       this.iframe_html = this.senetizer.bypassSecurityTrustResourceUrl(  this.spl);
      }
    });
  }

  toggleVideo() {
    this.videoplayer.nativeElement.play();
  }

}
