import { Component, OnInit } from '@angular/core';
import { AppService } from '../app.service';
import { FormGroup, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrModule } from 'ngx-toastr';


@Component({
  selector: 'app-post-management',
  templateUrl: './post-management.component.html',
  styleUrls: ['./post-management.component.css']
})
export class PostManagementComponent implements OnInit {

  temp:any = {};
  items:any = {};
  pageNumber:number = 1;
  limit:number=10;
  srNo:any;
  pageData:any;
  postForm:any;
  search:any = "";
  id: any;
  status: any;
  temp1: any;
  dateValue: any;
  fromDates: any;
  toDates: any;
  nowDate:any
  nowDate1:any
  element:any
  nowDate2:any
  constructor(
    private serviceCaller : AppService,
    private router : Router
  ) { }

  ngOnInit() {
     this.nowDate=new Date();
    this.nowDate1=new Date();
    this.nowDate2 = ''; 
    this.postForm = new FormGroup({
      search: new FormControl('',),
      toDate: new FormControl('',),
      fromDate: new FormControl('',) 
    });
    this.getList();
  }

searchFilter(searchData) {
    if(searchData.fromDate)
       this.fromDates = new Date(searchData.fromDate).toISOString();
    else
      this.fromDates = "";

    if(searchData.toDate)
      this.toDates =  (new Date(new Date(searchData.toDate).getTime() + 24 * 60 * 60 * 1000).toISOString());
    
    else
      this.toDates = "";

    this.temp.userId = "tggadfgda";
    this.temp.search = searchData.search;
    this.temp.limit = this.limit;
    this.temp.pageNumber = this.pageNumber;
    this.temp.fromDate = this.fromDates;
    this.temp.toDate = this.toDates;

    this.serviceCaller.postApi("/api/v1/admin/postList", this.temp, 0).subscribe((data:any) => {
      // console.log("@@@@@@@@@@@", data);
      if( data.response_code == 200 ) {
        this.items = data.result.docs;
        this.pageData = data.result;
        this.srNo = (this.pageNumber - 1) * 10
      }
    });
  }

  postDetail(detail) {
    // console.log('############', detail);
    this.router.navigate(['../../admin/postDetails/'+detail._id]);
  }

  getList() {
    this.temp.userId = localStorage.getItem('_id');
    this.temp.search = "";
    this.temp.fromDate = "";
    this.temp.toDate = "";
    this.temp.limit = this.limit;
    this.temp.pageNumber = this.pageNumber;
    

    this.serviceCaller.postApi("/api/v1/admin/postList", this.temp, 0).subscribe((data:any) => {
    //  console.log("@@@@@@@@@@@", data);
      if( data.response_code == 200 ) {
        this.items = data.result.docs;
        this.pageData = data.result;
        this.srNo = (this.pageNumber - 1) * 10
      }
    });
  }

  pagination($event){
    this.pageNumber = $event;
    this.getList();
  }

  block(id,status){
    this.id = id;
    if(status == 'ACTIVE') {
      this.status = 'INACTIVE';
    } else {
      this.status = 'ACTIVE';
    }
  }

  block_mod(){
    let a = {
      "postId" : this.id,
      "userId" : "dafgafgadfg",
      "status" : this.status
    }
    this.serviceCaller.postApi("/api/v1/admin/updatePostStatus",  a, 0).subscribe((data:any) => {
      if(data.response_code == 200) {
        this.serviceCaller.succ(data.response_message);
        this.getList();
      } else {
        this.getList();
      }
    });
  }


 Changed(event){
    if(event){
      this.nowDate2= event;
       this.nowDate= new Date();
    }
  }

  delete(id) {
    this.id = id;
  }

  deleteApi() {
    let deleteData = {
      "postId" : this.id,
      "userId" : localStorage.getItem('_id')
    }
    this.serviceCaller.postApi("/api/v1/admin/deletePost", deleteData, 0).subscribe((data:any) => {
      console.log('$$$$$$$$$$$$$$$', data)
      if(data.response_code == 200) {
        this.serviceCaller.succ(data.response_message);
      } else {
        this.serviceCaller.err(data.response_message);
      }
      this.getList();
    });
  }

}
