import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AppService } from '../app.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';

declare var $:any
@Component({
  selector: 'app-my-profile',
  templateUrl: './my-profile.component.html',
  styleUrls: ['./my-profile.component.css']
})
export class MyProfileComponent implements OnInit {
  id: any;
  userDetail: any;
  image = "data:image/jpeg;base64,"
  changePasswordForm: FormGroup;
  changeEmailForm: FormGroup;
  userEmail:any;
  constructor(public router: Router, private activatedRoute: ActivatedRoute, public service: AppService) { }

  ngOnInit() {
    this.getUserData()
    this.changePasswordForm = new FormGroup({
      oldPassword: new FormControl('', [Validators.required, Validators.minLength(8), Validators.maxLength(16)]),
      newPassword: new FormControl('', [Validators.required, Validators.minLength(8), Validators.maxLength(16)]),
      confirmPassword: new FormControl('', [Validators.required])
    })

    this.changeEmailForm = new FormGroup({
        email: new FormControl('',Validators.compose([Validators.required, Validators.maxLength(256),Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,3}))$/)])),
    })
  }

  getUserData() {
    let apireq = {
      userId: localStorage.getItem('_id')
    }
    this.service.postApi('/api/v1/admin/getUserDetail', apireq, 1).subscribe((success) => {
      console.log(success)
      if (success.response_code == 200) {
        this.userDetail = success.Data
        //this.image = success.Data.profilePic
        this.image = "https://res.cloudinary.com/sumit9211/image/upload/v1542879708/und4l1ruuang3cexhb9a.jpg";
        console.log("userDetails", this.userDetail)
        console.log(this.image)
      }
      else {
        console.log(success.response_message)
      }
    }, error => {
      console.log("Something went wrong")
    })
  }

  editProfile(data) {

    if (this.changePasswordForm.invalid || (data['newPassword'] != data['confirmPassword'])) {
      return
    }
    $('#edit_profile_modal').modal('hide')
   
    let apireq = {
      userId: localStorage.getItem('_id'),
      password: data.oldPassword,
      newPassword: data.newPassword
    }
    console.log("Api Data======>>>>>",apireq)

    this.service.postApi('/api/v1/admin/passwordChange', apireq, 1).subscribe(success => {
      console.log(success)
      if (success.response_code == 200) {
        this.router.navigate(['/admin/dashboard'])
        this.service.succ(success.response_message);
      } else {
       
        this.service.err(success.response_message);
      }
    }, error => {
      console.log("Something went wrong")
    })
  }


  editEmailProfile(dataa){
     if (this.changeEmailForm.invalid) {
      return
    }
    let apireq1 = {
      userId: localStorage.getItem('_id'),
      email: dataa.email
    }
    console.log(apireq1)
    this.service.postApi('/api/v1/admin/emailChange', apireq1, 1).subscribe(success => {
      console.log(success)
      if (success.response_code == 200) {
        $('#edit_profile_modal1').modal('hide')  
        this.service.succ(success.response_message);
        this.router.navigate(['/admin/dashboard'])
      }else {
        this.service.err(success.response_message);
        console.log(success.response_message)
      }
    }, error => {
      console.log("Something went wrong")
    })
  }

  reset() {
    this.changePasswordForm.controls['oldPassword'].setValue(null)
    this.changePasswordForm.controls['newPassword'].setValue(null)
    this.changePasswordForm.controls['confirmPassword'].setValue(null)
  }


    openModal(id,email){
    this.userEmail=email
    this.changeEmailForm.controls['email'].setValue(this.userEmail)
    $('#'+id).modal({ backdrop: 'static', keyboard: false });
  }
}
