import { Component, OnInit } from '@angular/core';

import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AppService } from '../app.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  LoginForm: FormGroup
  constructor(private route: Router, public spinnerService: Ng4LoadingSpinnerService, private service: AppService) { }

  ngOnInit() {
    this.LoginForm = new FormGroup({
      email: new FormControl('', [Validators.required, Validators.maxLength(256), Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,3}))$/)]),
      password: new FormControl('', [Validators.required, Validators.minLength(8), Validators.maxLength(16)]),
    });
    
  }

  loginFunction(loginData) {
    if (this.LoginForm.invalid) {
      return
    }
    if (loginData.checkbox) {
      localStorage.setItem('email', loginData.email)
      localStorage.setItem('password', loginData.password)
    }
    let apiData = {
      "email": loginData.email,
      "password": loginData.password,
      "role": 1
    }
    this.spinnerService.show();
    this.service.postApi('/api/v1/admin/adminLogin', apiData, 0).subscribe((success) => {
      console.log(success)
      if (success.response_code == 200) {
        localStorage.setItem('LOGINTOKEN', success.Data.jwtToken);
        localStorage.setItem("_id", success.Data._id)
        localStorage.setItem("role", success.Data.role)
        localStorage.setItem("name", success.Data.name)
        localStorage.setItem("profilePic", success.Data.profilePic)
        this.service.succ(success.response_message)
        this.spinnerService.hide();
        this.route.navigate(['/admin/dashboard'])
       
      }
      else {
        if (success.response_code == 500) {
          this.service.err(success.response_message)
          this.spinnerService.hide();
          console.log("error")
        } else {
          this.spinnerService.hide();
          this.service.err(success.response_message)
        }
      }
    }, error => {
      this.service.err("something went wrong")
      this.spinnerService.hide();
    })
  }

  forgotPassword() {
    this.route.navigate(['/forgotPassword'])
  }
}
