import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { AppService } from '../app.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-report-management',
  templateUrl: './report-management.component.html',
  styleUrls: ['./report-management.component.css']
})
export class ReportManagementComponent implements OnInit {
  searchForm: any;
  temp: any = {};
  items: any = {};
  pageNumber:number = 1;
  limit:number=10;
  srNo:any;
  pageData:any;
  id: any;
  search: any;
  ids: any;
  status: any;
  constructor(
    private serviceCaller : AppService,
    private router : Router
  ) { }

  ngOnInit() {
    this.formValidation();
    this.getList();
  }

  searchFilter(searchData) {
    this.search = searchData.search
    this.temp.userId = "tggadfgda";
    this.temp.search = this.search;
    this.temp.limit = this.limit;
    this.temp.pageNumber = this.pageNumber;

    this.serviceCaller.postApi("/api/v1/admin/reportList", this.temp, 0).subscribe((data:any) => {
      // console.log("@@@@@@@@@@@", data);
      if( data.response_code == 200 ) {
        this.items = data.data.result;
      this.pageData = data.data.result;
      this.srNo = (this.pageNumber - 1) * 10
      }
    });
  }

  formValidation(){
    this.searchForm = new FormGroup({
      search : new FormControl('',),
      fromDate : new FormControl('',),
      toDate : new FormControl('',)
    })
  }

  getList() {
    this.temp.userId = localStorage.getItem('_id');
    this.temp.search = "";
    this.temp.limit = this.limit;
    this.temp.pageNumber = this.pageNumber;
    this.serviceCaller.postApi("/api/v1/admin/reportList", this.temp, 0).subscribe((data:any) => {
      // console.log('@@@@@@@@@@@@', data.data.result);
      this.items = data.data.result;
      this.pageData = data.data.result;
      this.srNo = (this.pageNumber - 1) * 10
    });
  }

  pagination($event){
    this.pageNumber = $event;
    this.getList();
  }

  reportDetail(report) {
    this.router.navigate(['../../admin/reportDetails/'+report._id]);
  }

  delete(id) {
    this.id = id;
  }

  deleteApi() {
    let deleteData = {
      "reportId" : this.id,
      "userId" : localStorage.getItem('_id')
    }
    this.serviceCaller.postApi("/api/v1/admin/deleteReport", deleteData, 0).subscribe((data:any) => {
      if(data.response_code == 200) {
        this.serviceCaller.succ(data.response_message);
      } else {
        this.serviceCaller.err(data.response_message);
      }
      this.getList();
    });
  }


    block(id,status){
    this.ids = id;
    if(status == 'ACTIVE') {
      this.status = 'INACTIVE';
    } else {
      this.status = 'ACTIVE';
    }
  }

  block_mod(){
    let a = {
      "postId" : this.ids,
      "userId" : "dafgafgadfg",
      "status" : this.status
    }
    this.serviceCaller.postApi("/api/v1/admin/updatePostStatus",  a, 0).subscribe((data:any) => {
      if(data.response_code == 200) {
        this.serviceCaller.succ(data.response_message);
        this.getList();
      } else {
        this.getList();
      }
    });
  }

}
