import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { AppService } from '../app.service';
declare var $:any;
@Component({
  selector: 'app-user-management',
  templateUrl: './user-management.component.html',
  styleUrls: ['./user-management.component.css']
})
export class UserManagementComponent implements OnInit {

  searchForm:FormGroup;
  userArr:any=[];
  pageNumber=1;
  limit:any;
  total:any;
  srNumber:any;
  dateValue: any;
  userId: any;
  fromDates: any;
  toDates: any;
  nowDate:any
  nowDate1:any
  element:any
  nowDate2:any
  constructor(public service:AppService) { }

  ngOnInit() {
    this.nowDate=new Date();
    this.nowDate1=new Date();
    this.nowDate2 = ''; 
    this.searchFormVAlue()
    this.getViewData()
  }

  searchFormVAlue(){
    this.searchForm = new FormGroup({
      search : new FormControl('',),
      formDate : new FormControl('',),
      toDate : new FormControl('',)
    })
  }

  pagination(event){
    this.pageNumber = event
    this.getViewData()
  }

  getViewData(){
    let apireq={
      "userId":"fghfgu57567567",         
      "limit" : 10,  
      "pageNumber" : this.pageNumber,
      "search" : ""
    }
    this.service.postApi('/api/v1/admin/userList',apireq,1).subscribe((success:any)=>{
      if(success.response_code==200){
        this.userArr = success.Data.docs;
        this.limit = success.Data.limit;
        this.total = success.Data.total;
        this.srNumber = (this.pageNumber - 1) * 10;
      }
      else{
        this.service.err(success.response_message)
      }
    },error=>{
      this.service.err("Something went wrong")
    })
  }

  

  search(data){
    console.log(data)
    if(data.formDate)
       this.fromDates = new Date(data.formDate).toISOString();
    else
      this.fromDates = "";

    if(data.toDate)
      this.toDates =  (new Date(new Date(data.toDate).getTime() + 24 * 60 * 60 * 1000).toISOString());
    
    else
      this.toDates = "";

    let apireq={
      "userId":"fghfgu57567567",         
      "limit" : 10,  
      "pageNumber" : this.pageNumber,
      "search" : data.search,
      "fromDate" : this.fromDates,
      "toDate" : this.toDates 
    }
    console.log(apireq)
    this.service.postApi('/api/v1/admin/userList',apireq,1).subscribe((success:any)=>{
      console.log(success)
      if(success.response_code==200){
        this.userArr = success.Data.docs;
        this.limit = success.Data.limit;
        this.total = success.Data.total;
      }
      else{
        this.service.err(success.response_message)
      }
    },error=>{
      this.service.err("Something went wrong")
    })
  }

  deleteApi(status){
    let apireq={
      "userId" : this.userId
    }
    this.service.postApi('/api/v1/admin/deleteUser',apireq,1).subscribe(success=>{
      if(success.response_code==200){
        this.getViewData();
        $('#delete').modal('hide')
        this.service.succ(success.response_message)
      }
      else{
        this.service.err(success.response_message)
      }
    },error=>{
      this.service.err("Something went wrong")
    })
  }

  blockApi(status,modal){
    console.log('##########',status)
    let apireq={
      "userId":this.userId,
      "status":status
    }

    this.service.postApi('/api/v1/admin/updateUserStatus',apireq,1).subscribe((success:any)=>{
      console.log(success)
      if(success.response_code==200){
        this.getViewData()
        $('#'+modal).modal('hide')
        this.service.succ(success.response_message)
      }
      else{
        this.service.err(success.response_message)
      }
    },error=>{
      this.service.err("Something went wrong")
    })
  }

  openModal(id,userId){
    this.userId=userId
    $('#'+id).modal({ backdrop: 'static', keyboard: false });
  }

  // changeDate(){
  //   this.dateValue=new Date(this.searchForm.value.formDate)
  //   console.log(this.dateValue)
  // }

    Changed(event){
    if(event){
      this.nowDate2= event;
       this.nowDate= new Date();
    }
  }
}
